import React, { Fragment } from 'react'
import { Link } from 'react-router-dom'

import MetaData from '../layout/MetaData'
import CheckoutSteps from './CheckoutSteps'

import { useSelector } from 'react-redux'

const ConfirmOrder = ({ history }) => {

    const { cartItems, shippingInfo } = useSelector(state => state.cart)
    const { user } = useSelector(state => state.auth)

    // Calculate Order Prices
    const itemsPrice = cartItems.reduce((acc, item) => acc + item.price * item.quantity, 0) // ging ging nh for, foreach
    const shippingPrice = itemsPrice > 100000 ? 0 : 10000 // tnh tin ship
    const taxPrice = Number((0.01 * itemsPrice)) // tnh thu
    const totalPrice = (itemsPrice + shippingPrice + taxPrice) // tng tin

    const processToPayment = () => {
        const data = {
            itemsPrice: itemsPrice,
            shippingPrice,
            taxPrice,
            totalPrice
        }

        sessionStorage.setItem('orderInfo', JSON.stringify(data)) // push session order Info
        history.push('/payment')
    }

    return (
        <Fragment>

            <MetaData title={'Xc nhn ha n'} />

            <CheckoutSteps shipping confirmOrder />

            <div className="row d-flex justify-content-between">
                <div className="col-12 col-lg-8 mt-5 order-confirm">

                    <h4 className="mb-3">Thng tin vn chuyn</h4>
                    <p><b>Tn khch hng:</b> {user && user.name}</p>
                    <p><b>S in thoi:</b> {shippingInfo.phoneNo}</p>
                    <p className="mb-4"><b>a ch:</b> {`${shippingInfo.address}, ${shippingInfo.city}, ${shippingInfo.postalCode}, ${shippingInfo.country}`}</p>

                    <hr />
                    <h4 className="mt-4">Cc mt hng trong gi hng ca bn:</h4>

                    {cartItems.map(item => (
                        <Fragment>
                            <hr />
                            <div className="cart-item my-1" key={item.product}>
                                <div className="row">
                                    <div className="col-4 col-lg-2">
                                        <img src={item.image} alt="Laptop" height="45" width="65" />
                                    </div>

                                    <div className="col-5 col-lg-6">
                                        <Link to={`/product/${item.product}`}>{item.name}</Link>
                                    </div>

                                    <div className="col-4 col-lg-4 mt-4 mt-lg-0">
                                        <p>{item.quantity} x {(item.price).toLocaleString()} = <b>{(item.quantity * item.price).toLocaleString()}</b></p>
                                    </div>

                                </div>
                            </div>
                            <hr />
                        </Fragment>
                    ))}



                </div>

                <div className="col-12 col-lg-3 my-4">
                    <div id="order_summary">
                        <h4>Tng gi tr n hng</h4>
                        <hr />
                        <p>Thnh tin:  <span className="order-summary-values">{(itemsPrice).toLocaleString()}</span></p>
                        <p>Ph vn chuyn: <span className="order-summary-values">{(shippingPrice).toLocaleString()}</span></p>
                        <p>Thu:  <span className="order-summary-values">{(taxPrice).toLocaleString()}</span></p>

                        <hr />

                        <p>Tng tin: <span className="order-summary-values">{(totalPrice).toLocaleString()}</span></p>

                        <hr />
                        <button id="checkout_btn" className="btn btn-primary btn-block" onClick={processToPayment}>Tin hnh thanh ton</button>
                    </div>
                </div>


            </div>

        </Fragment>
    )
}

export default ConfirmOrder
