import React from 'react'
import { Link } from 'react-router-dom'

const Sidebar = () => {
    return (
        <div className="sidebar-wrapper">
            <nav id="sidebar">
                <ul className="list-unstyled components">
                    <li>
                        <Link to="/dashboard"><i className="fa fa-tachometer"></i> Tng quan</Link>
                    </li>
                    <li>
                        <Link to="/admin/products"><i
                            className="fa fa-product-hunt"></i> Qun l sn phm</Link>
                    </li>

                    <li>
                        <Link to="/admin/orders"><i className="fa fa-shopping-basket"></i> Qun l n hng</Link>
                    </li>

                    <li>
                        <Link to="/admin/users"><i className="fa fa-users"></i> Qun l ngi dng</Link>
                    </li>

                    <li>
                        <Link to="/admin/reviews"><i className="fa fa-star"></i> Qun l bnh lun</Link>
                    </li>

                </ul>
            </nav>
        </div>
    )
}

export default Sidebar
